import i18n from 'i18next';
import type { InitOptions } from 'i18next';
import { initReactI18next } from 'react-i18next';
import discussionEn from './locales/en/discussion.json';

enum Locale {
  English = 'en',
}

export enum Namespace {
  Discussion = 'discussion',
}

export const RESOURCES: InitOptions['resources'] = {
  [Locale.English]: {
    [Namespace.Discussion]: discussionEn,
  },
};

i18n.use(initReactI18next).init({
  resources: RESOURCES,
  ns: [Namespace.Discussion],
  lng: Locale.English,
  interpolation: {
    escapeValue: false,
  },
});
export default i18n;
