import React, { FC, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { Discussion } from '../../../../typings';
import styles from './DiscussionCard.module.scss';
import { fromUnixTime, formatDistance } from 'date-fns';
import Tag from '../Tag';
import SpectrumBar from '../SpectrumBar';

type DiscussionCardProps = {
  discussion: Discussion;
};

const DiscussionCard: FC<DiscussionCardProps> = ({ discussion }) => {
  const { t } = useTranslation('discussion');
  const photoAuthor = discussion?.photoAuthor?.name;
  const endDate =
    discussion?.endsAt && fromUnixTime(Number(discussion?.endsAt) / 1000);
  const formattedTimeLeft = endDate && formatDistance(endDate, new Date());

  const renderCounterInfo = useMemo(
    () => (
      <>
        <span className={styles.opinion_count}>
          {t('card.opinion', { count: discussion?.opinionsCount.Total })}
        </span>
        <div className={styles.responses_row}>
          <span className={styles.responses_count}>
            {t('card.response', {
              count: discussion?.responsesCount.Total,
            })}
          </span>
          <span className={styles.responses_count}>
            {discussion?.isFinished
              ? t('card.ended')
              : t('card.time_left', { time: formattedTimeLeft })}
          </span>
        </div>
      </>
    ),
    [
      discussion?.isFinished,
      discussion?.opinionsCount.Total,
      discussion?.responsesCount.Total,
      formattedTimeLeft,
      t,
    ],
  );

  const renderLink = useMemo(
    () => (
      <a className={styles.discussion_link} href={discussion?.deepLink}>
        {t('card.join_conversation')}
      </a>
    ),
    [discussion?.deepLink, t],
  );

  return (
    <div className={styles.discussion_card_container}>
      <div className={styles.main_content}>
        <div className={styles.general_info}>
          <img
            className={styles.discussion_picture}
            src={discussion?.backgroundPictureUrl}
          />
          <div className={styles.counter_info__desktop}>
            {renderCounterInfo}
          </div>
          <div className={styles.desktop_link}>{renderLink}</div>
        </div>
        <div className={styles.discussion_info}>
          <h3 className="title">{discussion.title}</h3>
          <div className={styles.user_info_container}>
            <img
              className={styles.avatar}
              src={discussion?.author.pictureUrl}
            />
            <span className={styles.user_name}>
              {discussion?.author.fullName}
            </span>
          </div>
          <p>{discussion.description}</p>
          <div className={styles.tags_container}>
            {discussion.tags.map((tag, index) => (
              <Tag key={index} tag={tag} />
            ))}
          </div>
          <div className={styles.unsplashAuthor}>
            {photoAuthor ? t('card.photo_by') : t('card.photo')}
            {photoAuthor && (
              <a href={discussion?.photoAuthor?.profileUrl}>{photoAuthor}</a>
            )}

            {t('card.on')}
            <a href="https://unsplash.com/">{t('card.unsplash')}</a>
          </div>
          <div className={styles.counter_info__mobile}>{renderCounterInfo}</div>
        </div>
      </div>
      <div className={styles.mobile_link}>{renderLink}</div>
      <SpectrumBar opinionsCount={discussion?.opinionsCount} />
    </div>
  );
};

export default DiscussionCard;
