import React, { FC } from 'react';
import {
  ApolloClient,
  ApolloProvider as ApolloClientProvider,
  createHttpLink,
  FieldPolicy,
  InMemoryCache,
} from '@apollo/client';
import { uniqBy } from 'lodash';

const ApolloProvider: FC = ({ children }) => {
  const httpLink = createHttpLink({
    uri: process.env.REACT_APP_GRAPHQL_URL,
  });

  // temporary solution with any
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const mergeWithUniqId = (existing = [], incoming: any) => {
    return uniqBy([...existing, ...incoming], '__ref');
  };

  const defaultMergePolicy: FieldPolicy = {
    keyArgs: false,
    merge: mergeWithUniqId,
  };

  const client = new ApolloClient({
    link: httpLink,
    cache: new InMemoryCache({
      // @ts-ignore
      connectToDevTools: process.env.NODE_ENV !== 'production',
      typePolicies: {
        Query: {
          fields: {
            interestingDiscussions: defaultMergePolicy,
          },
        },
      },
    }),
  });

  return (
    <ApolloClientProvider client={client}>{children}</ApolloClientProvider>
  );
};

export default ApolloProvider;
