import React, { FC } from 'react';
import { Discussion } from '../../../../typings';
import styles from './SpectrumBar.module.scss';

type SpectrumBar = {
  opinionsCount: Discussion['opinionsCount'];
};

const brightGreen = '#36BA8E';
const green = '#218274';
const blue = '#2553B8';
const purple = '#822157';

const SpectrumBar: FC<SpectrumBar> = ({ opinionsCount }) => {
  const getWidth = (opinion: keyof Discussion['opinionsCount']) =>
    opinionsCount.Total > 0
      ? `${((opinionsCount[opinion] * 100) / opinionsCount.Total).toFixed()}%`
      : '0%';

  const getOptionStyle = (
    opinion: keyof Discussion['opinionsCount'],
    backgroundColor: string,
  ) => ({ width: getWidth(opinion), backgroundColor });

  return (
    <div className={styles.container}>
      <div
        className={styles.element}
        style={getOptionStyle('AgreeAnd', brightGreen)}
      />
      <div
        className={styles.element}
        style={getOptionStyle('AgreeBut', green)}
      />
      <div className={styles.element} style={getOptionStyle('Neutral', blue)} />
      <div
        className={styles.element}
        style={getOptionStyle('Disagree', purple)}
      />
    </div>
  );
};

export default SpectrumBar;
