import { gql } from '@apollo/client';

export const GET_FEATURED_DISCUSSIONS = gql`
  query GET_FEATURED_DISCUSSIONS {
    featuredDiscussions {
      id
      isFinished
      endsAt
      backgroundPictureUrl
      title
      description
      responsesCount {
        Total
      }
      tags
      photoAuthor {
        name
        profileUrl
        portfolioUrl
      }
      opinionsCount {
        Total
        Neutral
        AgreeAnd
        AgreeBut
        Disagree
      }
      deepLink
      author {
        fullName
        pictureUrl
      }
    }
  }
`;
